body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face
{
font-family: GeoSansLight;
src: url(/static/media/GeosansLight.36e6a06a.ttf),
url(/static/media/GeosansLight.249147d3.eot);
}

.test {
  color: white!important;
  z-index: 100; 
  width: 30px; 
  height: 30px; 
  border-color: transparent; 
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255,255,255, 0.9)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E")!important;
}

.nav-link {
  font-size: 15px !important; 
}

.navbar-light, .light-nav {
  color: white!important; 
  border-color: transparent!important;  
}

.navbar-light .navbar-toggler {
  border-color: transparent!important;
}

.navbar-light .navbar-toggler:focus {
  outline:none!important; 
}

.dropdown-menu {
  background-color: transparent; 
  border-color: transparent!important; 
}

a {
  color: inherit; 
  font-family: Helvetica Neue,sans-serif !important; 
  font-size: 0.99em; 
}
h1, h2, h3 {
  font-family: Helvetica Neue,sans-serif !important;  
}

h2 {
  text-align: center;
  letter-spacing: 0.1em;
}

h1 {
  font-size: 2.0em; 
}

h3 {
  font-size: 1.3em!important; 
}

div {
  font-family: Helvetica Neue,sans-serif !important; 
  font-size: 0.99em; 
}

.quietLink {
  color: inherit; 
}
.quietLink:hover {
  color: inherit;
  text-decoration: none;
}

.headerLink {
  color: darkslategrey; 
  text-decoration: none !important;
  letter-spacing: 0.25em;
  font-size: 16px; 
}
.headerLink:hover {
  color: black; 
  text-decoration: none !important;
}

.heading {
  padding: 15px; 
}

.nav-link {
  letter-spacing: 2px; 
}

.sticky {
  position: sticky; 
  position: -webkit-sticky; 
  z-index: 2; 
  top: 0;
  height: 65px; 
  width: 100vw; 
  background-color: white!important; 
}

.content {
  z-index: 1;
}

.sectionheader {
  width: 100vw; 
  position: absolute; 
  text-align: center; 
  font-size: 2rem; 
  margin-top: 15px;
  letter-spacing: 0.1em;
  left: 0; 
  color: black!important; 
  font-weight: 500; 
  background-color: white; 
  z-index: 1; 
  vertical-align: bottom;
  display: flex!important; 
  justify-content: center; 
  height: 136px; 
}

.sectionheadernotext {
  width: 100vw; 
  position: absolute; 
  text-align: center; 
  font-size: 2rem; 
  margin-top: 15px;
  letter-spacing: 0.1em;
  left: 0; 
  color: black!important; 
  font-weight: 500; 
  background-color: white; 
  z-index: 1; 
  vertical-align: bottom;
  display: flex!important; 
  justify-content: center; 
  height: 45px; 
}


.downloadicon {
  min-width: 15px;
  width: 2vw;
  max-width: 28px;
  position: absolute; 
  margin-left: 3vw; 
  align-self: flex-end;  
}

.sectionheader span {
  align-self: flex-end;
}

@media only screen and (min-width: 800px) {
  .headerLink {
      font-size: 24px; 
  }
  .downloadicon {
    -webkit-transform: translate(0px, -4px);
            transform: translate(0px, -4px);
  }
}   

@media only screen and (max-width: 766px) {
  a {
    font-size: 12px!important; 
  }
  div {
    font-size: 12px!important;
  }

  .sectionheader {
    margin-top: 15px;
    height: 100px;
  }

}

@media only screen and (max-height: 799px) {
  .sectionheadernotext {
    height: 10px; 
  }
}

#viewingimage {
    max-width: 80vw;
    max-height: 80vh;
    position: relative; 
}

#imagecontainer {
    width: 100vw; 
    height: 100vh;
}

#infocontainer {
    width: 100vw; 
    margin-bottom: 2vh;
    margin-top: 10px;
    text-align:right;
}

#infoicon {
    margin-right:49.4%;
    position: relative; 
    cursor: pointer; 
    width: 3vh; 
    min-width: 20px;
}

.icon {
    position: relative; 
    cursor: pointer; 
    width: 3vh; 
    min-width: 20px;
    margin-right: 12px; 
}

.quietLinkIcon {
    text-decoration: none;
}

.quietLinkIcon:hover {
    text-decoration: none;
    color: inherit;
}

.directionarrows {
    cursor: pointer;
    top: 50%;
    position: relative;
    width: 2vw;
    min-width: 30px;
}

.directionarrowscontainer {
    align-items: center;
    width: 10vw;
    height: 80vh; 
    padding: 0px;
}

.infoviewingimage {
    top: 50%; 
    max-width: 80vw; 
    max-height: 80vh; 
    align-self: left;
}

.imageviewingcontainer {
    align-self: center;
    max-width: 80vw; 
}

.col-xl-6, .col-sm-12, .col-lg-12, .col-12, .col-md-12, .col-xs-12, .container {
    padding-right: 0px!important; 
    padding-left: 0px!important; 
}

.row {
    margin-left: 0px!important; 
    margin-right: 0px!important; 
}

.infoviewingimage {
    max-width: 80vw; 
    align-self: center;
    
}
.infotext {
    top: 5%;
    height: 40vh;
    font-size: 12px;
    text-align: center;
    color: darkslategrey; 
}

#imagewall {
    vertical-align: center; 
}

#frame {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

@media only screen and (orientation: portrait) {
    #frame {
         flex-direction: column;
    }
    .directionarrows {
        top: 25%;
    }
    
}

@media only screen and (orientation: landscape) {
    #frame {
         flex-direction: row;
    }
    .infotext {
        top: 40%;
        text-align: left;
        position: relative;
        width: 30vh; 
    }
}

@media only screen and (max-width: 1999px) {
    #imagewall {
        vertical-align: text-top;
    }
}

@media only screen and (max-width: 1100px) {
    .infotext {
        margin-top: 3vh; 
    }
}

@media only screen and (max-height: 600px) {
    #infocontainer {
        margin-top: 10px;
    }
    #infoicon {
        margin-right: 48%;
    }
}  


@media only screen and (max-width: 800px) {
    #infocontainer {
        margin-top: 10px;
    }
    #infoicon {
        margin-right: 48%;
    }
}  

@media only screen and (orientation: landscape) and (max-width: 1300px) {
    .infotext {
        padding-left: 30px;
    }
}

@media only screen and (min-width: 1200px) {

    #frame {
        flex-direction: row;
    }
    .infotext {
        top: 40%;
        text-align: left;
        position: relative;
        width: 30vh; 
    }
    
}    

@media only screen and (min-width: 1500px) {
    .infoviewingimage {
        max-width: 80vw; 
        align-self: center;
    }
    #frame {
        justify-content: space-around;
    }
} 

@media only screen and (min-width: 1800px) {
    .infoviewingimage {
        max-width: 80vw; 
        align-self: center;
        
    }
} 

@media only screen and (min-width: 2400px) {
    .infoviewingimage {
        max-width: 80vw; 
        align-self: center;
        
    }
    #nav-link {
        letter-spacing: 3px; 
    }
} 
.documentwindow {
    text-align: center; 
    width: 100%; 
    max-width: 100vh; 
} 


.downloadicondiv {
    width: 100vw; 
    left: 0; 
}
.newsBlock {
    margin-top: 8vh;
    margin-bottom: 10px; 
    align-content: flex-start;
    display: flex;
    width: 50%;
    padding-left: 25%;
}

.newsHeader {
    font-style: italic; 
    font-weight: bold;
    text-align: left; 
}

.newsText {
    color: black; 
    text-align: left; 
}

.vimeoVideo {
    padding: 40% 0 0 0;
    position: relative; 
    max-width: 50%;
    max-height: 50%; 
    margin: auto;
}

@media only screen and (orientation: portrait) {
    .vimeoVideo {
        max-width: 90%; 
        max-height: 90%; 
    }
    .newsBlock {
        align-content: center;
        display: flex;
        width: 100%;
        padding-left: 15%;
    }
}   

#aboutcontent {
    margin-top: 30px; 
    text-align: center;
}

#aboutheader {
    margin-top: 5vh; 
    margin-left: 18vw; 
    margin-right: 18vw; 
    text-align: justify; 
}

#abouttext {
    -webkit-column-count: 1;
            column-count: 1; 
    grid-column-gap: 5vw; 
    -webkit-column-gap: 5vw; 
            column-gap: 5vw; 
    margin-top: 4vh;
    margin-left: 18vw; 
    margin-right: 18vw; 
    text-align: justify; 
}

.aboutparagraph { 
    margin-bottom: 10px; 
}

.sticky {
    top: -1px; 
    padding-top: 1px;
}

@media only screen and (orientation: landscape) {
